import React from 'react';
import Grid from '@material-ui/core/Grid';

import STRINGS from '../../../../../strings';
import { BankRadioButtons } from '../../../../../components/RadioButtons';
import { BacLogoImg, MultibankLogoImg, StGeorgeLogoImg, GlobalBankLogoImg } from '../../../../../components/Images';
import { LaboralEntity, LaboralSector } from './index';
import { Props as QuotationProps } from '../index';

let options:any = [{}]

interface Props extends QuotationProps {
  laboralEntitys: LaboralEntity[];
  sectors: LaboralSector[];
};

function Banks(props: Props) {
  const { quotation, setQuotation, laboralEntitys, sectors } = props;

  function onOptionPressed(bank: string) {
    setQuotation({ loan: { bank } });
  }
  
  const calcBank = (laboralEntity: LaboralEntity) => {

    // console.log(laboralEntity);
  
    let { 
      ruta,
      comision,
      debt_capacity,
      debt_capacity_mortgage,
      discount_capacity,
      discount_capacity_mortgage,
      mount_max,
      mount_min,
      plazo_max,
      tasa } = laboralEntity 
  
    // Este valor viene del cálculo de la pantalla anterior
    if(term !== undefined) plazo_max = term

    let salary = wage ? wage : 0
    if(alloance !== undefined) salary += alloance
    if(perDiem !== undefined) salary += perDiem
  
    let segVida = 0.1  // Pendiente definicion
    let feci = 1.0  // Pendiente definicion
  
    let mortage = monthlyResidenceFee ? monthlyResidenceFee : 0.00
  
    let pje_dscto = discount_capacity ? discount_capacity : 0.00
    if(mortage > 0) pje_dscto = discount_capacity_mortgage ? discount_capacity_mortgage : 0.00
    let pje_debt = debt_capacity ? debt_capacity : 0.00
    if(mortage > 0) pje_debt = debt_capacity_mortgage ? debt_capacity_mortgage : 0.00
  
    const cap_discto = salary * pje_dscto / 100.00
    const cap_debt = salary * pje_debt / 100.00
  
    const cap_debt_available = cap_debt - mortage
    let cap_discto_available = cap_debt_available < cap_discto ? cap_debt_available : cap_discto
  
    const fcot = new Date()
    let ffpay = new Date(fcot.getTime() + ((pje_dscto > 20 ? 61 : 31)*24*60*60*1000))
    let fvencto = ffpay
    let ff = ffpay
  
    // plazo_max = plazo_max ? plazo_max : 0
    tasa = tasa ? tasa : 0
    mount_max = mount_max ? mount_max : 0
    mount_min = mount_min ? mount_min : 0
  
    // Datos cargado para probar la formula
    // plazo_max = 120
    // tasa = 9.75
    // cap_discto_available = 154.05
  
    let yyyy = ffpay.getFullYear()
    let mm = ffpay.getMonth() + 1
    let dd = ffpay.getDate()
    const dd2 = dd
    // console.log(yyyy,mm,dd,dd2);
    let ftemp = ''
  
    for(let i=0; i<plazo_max; i++){
  
      if(mm === 11) {
        mm = 1
        yyyy += 1
      } else mm += 1
  
      if(dd2 > 30 && (mm === 4 || mm === 6 || mm === 9 || mm === 11 )) dd = 30
      else if(dd2 > 27 && mm === 2) {dd = 28}
      else {dd = dd2}
  
      ftemp = yyyy.toString() + '-' + ((mm < 10) ? '0'+mm.toString() : mm.toString()) + '-' + ((dd < 10) ? '0'+dd.toString() : dd.toString())
  
      fvencto = new Date(ftemp)
      ff = new Date(fvencto.getTime() + 24*60*60*1000)
      fvencto = ff
  
      // console.log(i, yyyy,mm,dd, fvencto);
  
    }
  
    const daysLoan = Math.round((fvencto.getTime() - ffpay.getTime())/(24*60*60*1000))
    const avgDaysLoan = Math.round((daysLoan / plazo_max)*100)/100
    const paysYear = Math.round((360/avgDaysLoan)*100)/100
    const tasaMes = tasa/(paysYear ? paysYear : 1)/100
    const factor = (1-(1+tasaMes)**(-plazo_max))/tasaMes
    const reFactor = tasaMes/(1-(1+tasaMes)**(-plazo_max))

    let loan = Math.round((factor*cap_discto_available)/(1+(factor*segVida/100*1.07)))
    if(loan > 5000) {
      console.log('Loan > 5000 FCI:',loan);
      const tasaMes = (tasa+feci)/(paysYear ? paysYear : 1)/100
      const factor = (1-(1+tasaMes)**(-plazo_max))/tasaMes
      loan = Math.round((factor*cap_discto_available)/(1+(factor*segVida/100*1.07)))
    }

    const letraRetenida = false
    const monthlyFee = Math.round(reFactor*loan*100)/100
    const comisClose = Math.round(comision*loan)/100
    const comisItbm = Math.round(comisClose*0.07*100)/100
    const gastoLegal = 0
    const timbres = 0
    const servicioDescto = 0
    const montoLetRet = letraRetenida ? monthlyFee : 0

    const aRecibir = loan - (comisClose+comisItbm+gastoLegal+timbres+servicioDescto+montoLetRet)

    let nota = ''
    if(loan > mount_max) {
      nota = `*** Monto del préstamo por encima del Máximo. Monto calculado: ${loan} ***`
      loan = mount_max
    }
    if(loan < mount_min) nota = '*** Monto del préstamo por debajo del mínimo. ***'

    const available = {
      'aRecibir': aRecibir,
      'letraRetenida': letraRetenida,
      'monthlyFee': monthlyFee,
      'comisClose': comisClose,
      'comisItbm': comisItbm,
      'gastoLegal': gastoLegal,
      'timbres': timbres,
      'servicioDescto': servicioDescto,
      'montoLetRet': montoLetRet,
      'factor':factor,
      'LOAN':loan,
      'Observación': nota
    }
  
    console.log(available);
    options.push(
      {
        logo: ruta==='200' ? BacLogoImg : (ruta==='300' ? MultibankLogoImg : (ruta==='400' ? StGeorgeLogoImg : GlobalBankLogoImg )),
        value: ruta,
        amount: loan,
        payments: plazo_max,
        monthlyFee: monthlyFee
      }
    ) 
  }
  
  // let { monthlyResidenceFee, wage, alloance, perDiem, jobSector, profession, occupation } = quotation.borrower
  let { monthlyResidenceFee, wage, alloance, perDiem, jobSector, profession } = quotation.borrower

  // Valores calculados en la pantalla anterior
  let { amount, term } = quotation.loan

  // bank,reason

  // console.log(quotation)
  profession = profession ? profession : '0'
  const idSectors = sectors.filter(st => st.sector === jobSector)
  const idSector = idSectors[0].id_sector
 
  // const keyTarifas = {
  //   'Sector': jobSector,
  //   'idSector': idSector,
  //   'Profesion': profession,
  //   'Ocupacion': occupation
  // }

  const laboralEntities = laboralEntitys.filter(
    le => le.id_sector === idSector && 
    le.id_profesion === Number(profession) && le.ruta !== '100')

  options=[]
  laboralEntities.forEach(le => {
    calcBank(le)
  })

  return (
    <Grid container spacing={2} justify='center'>
      <Grid item xs={11}>
        <BankRadioButtons
          label={STRINGS.QUOTATION.PERSONAL_LOAN.LOAN.CHOOSE_BANK}
          options={options}
          onClick={onOptionPressed}
          value={quotation.loan.bank}
        />
      </Grid>
    </Grid>
  );
};

export default Banks;